import { Component, OnInit } from '@angular/core';
import { AppService } from '../services/app.service';
import { UserService } from '../user.service';



@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {

  constructor(private _AppService:AppService, private _UserService:UserService) { }

  errorMessage: string = ''
  hold_btn = false

  rules = false

  user_register(form) {
    let _value = form.value
    console.log(_value)
    this._AppService.post_data(`auth/register`,{
      "name":_value.user_name,
      "email":_value.email,
      "password":_value.password,
      "device_id":localStorage.getItem('device_id'),
      "device_type":this._AppService.get_platform(),
      "token":localStorage.getItem('fcm_token')
    },'s').subscribe((res: any)=>{
      this.errorMessage = ''
      console.log(res)
      if(res.status == 1){
        //-- success
        console.log(res.data)
        localStorage.setItem('token',res.data.token)
        this._AppService.router_navigation('/favorites-competitions')
      }
    },
    error => {
        if(error.status == 422){
          if(error.error.data.email[0] == 'Email already used!'){
            console.log('yup')
            this.errorMessage = 'هذا البريد الإلكتروني مستخدم من قبل'
          }
        }  
    })
  }

  back(){
    this._AppService.go_back()
  }
  
  _rules(){
    if(this.rules){
      this.rules = false
    } else {
      this.rules = true
    }
  }


  ngOnInit() {
    this._UserService.is_user_logged_routing()
  }

}
